import React, { useContext } from "react";
import { withStyles } from "@mui/styles";
import style from "./styles/WhoAreWeStyle";
import { Typography } from "@mui/material";
import { LanguageContext } from "./context/LanguageContext";
import words from "./text/WhoAreWeWords";

function WhoAreWe(props) {
  const { classes } = props;

  const { language } = useContext(LanguageContext);

  const { title, history, societies } = words[language];

  return (
    <div className={classes.container}>
      <Typography variant="h1">{title}</Typography>
      <div className={classes.description}>
        {language === "spanish" ? (
          <p className={classes.paragraphHistory}>
            Tenemos el orgullo de decir que somos el primer restaurante en el
            Valle de Cocora, gestores de la corriente turstica hacia la regin
            con una tradicin de ms de 30 aos en el departamento del Quindo.
            <br />
            El Eco-Hotel Las Palmas de Cocora ha sido desde hace ms de 30 aos
            un gran pilar en el turismo de la zona, precursores en la hotelera
            y restaurantera. <br />
          </p>
        ) : (
          <p className={classes.paragraphHistory}>
            We are proud to say that we are the first restaurant in the Cocora
            Valley, we are pioneers in the region tourism with more than 30
            years of tradition in the department of El Quindo
          </p>
        )}

        <img
          className={classes.imgEntrance}
          src={require("./images/Entrada.jpeg")}
          alt="Not found"
        />
      </div>
      <div className={classes.ecoFriendly}>
        <Typography variant="h4">
          {language === "spanish"
            ? "Somos Eco Amigables!"
            : "We are Ecore Friendly!"}
        </Typography>
        <p
          style={{
            width: "200px",
            marginRight: "auto",
            marginLeft: "auto",
            fontSize: "1.3em",
            color: "#252525",
          }}
        >
          {language === "spanish"
            ? "Somos conscientes de la gran importancia que tiene el cuidar de nuestro planeta. Es por eso que nos aseguramos de tener el menor impacto en el medio ambiente!"
            : "We are aware of the great importance that is taking care of our planet. That is why we make sure we have a minimal impact in the Eco System!"}
        </p>
      </div>
      <div style={{ backgroundColor: "#b8e29f" }}>
        <Typography variant="h3">{history}</Typography>
        <div className={classes.history}>
          {language === "spanish" ? (
            <p className={classes.paragraph}>
              Desde sus inicios, Las Palmas de Cocora han sido un restaurante
              familiar, fundado hace ms de 30 aos por el ganadero Alberto
              Prez Puerta y su esposa Maria Teresa Puerta Manzano.
              <br />
              Fue el primer restaurante que apareci en el Valle de Cocora
              impulsando as el turismo y el desarrollo de la regin.
              <br />
              Hemos sido durante aos un negocio familiar, por lo que nos
              preocupamos por hacer que cada quin se sienta como en su casa
            </p>
          ) : (
            <p className={classes.paragraph}>
              Since its beginning, Las Palmas de Cocora has been a family
              restaurant, founded 30 years ago by the farmer Alberto Prez
              Puerta alongside with his wife Maria Teresa Puerta Manzano.
              <br />
              It was the first restaurant in the Valley, with its arrival, the
              region was stormed by tourists of all around the world.
              <br />
              As a family business, we know what matters the most when it comes
              to going out with your family, so we make every effort for you to
              feel like you are at home.
            </p>
          )}

          <img
            className={classes.img}
            src={require("./images/MariaTYAlberto.jpeg")}
            alt="img Not found"
          />
          {language === "spanish" ? (
            <p className={classes.paragraph}>
              Todos nuestros esfuerzos siempre han sido y siempre sern para
              hacer de este un lugar acogedor y poder pasar un momento en
              familia impregnandose de la naturaleza y de la hermosura del
              Valle.
            </p>
          ) : (
            <p className={classes.paragraph}>
              All our efforts have always been and will always be to make this a
              welcoming place where families can spend time together, immersing
              themselves in nature and the beauty of the valley.
            </p>
          )}
        </div>
      </div>

      <div>
        <Typography variant="h3">{societies}</Typography>
        <div className={classes.society}>
          <img
            className={classes.imgCertificate}
            src={require("./images/CertificacionPalmas.png")}
            alt="Error: Not Found"
          />
        </div>
      </div>
    </div>
  );
}

export default withStyles(style)(WhoAreWe);
